.Slider {
    font-size: .75em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
}

.switch-path {
    background-color: black;
    border: .1em solid gray;
    width: 3em;
    height: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 .2em 0 .2em;
    position: relative;
}

.switch {
    width: 40%;
    height: 100%;
    background-color: lightblue;
    position: absolute;
}



